.loader-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 20;
  background-color: white;
  transition: background-color 500ms;
  cursor: wait;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-direction: normal;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }
  .loader-wrapper .logo {
    width: 60px; }
  .loader-wrapper .message {
    margin-top: 16px;
    text-transform: uppercase;
    letter-spacing: 2px;
    font-weight: 600; }
  .loader-wrapper .message:after {
    overflow: hidden;
    position: absolute;
    -webkit-animation: ellipsis steps(4, end) 4s infinite;
    animation: ellipsis steps(4, end) 4s infinite;
    content: "...";
    width: 0px; }

.loader-wrapper.fixed {
  position: fixed;
  top: initial;
  left: initial; }

@keyframes ellipsis {
  to {
    width: 20px; } }

@-webkit-keyframes ellipsis {
  to {
    width: 20px; } }

.loader {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 20;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  flex-direction: column;
  -webkit-box-direction: normal;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

@media (min-width: 1000px) {
  main .loader-wrapper.fixed {
    width: calc(100% - 250px); } }

@keyframes cssload-rotate {
  100% {
    transform: rotate(360deg); } }

@-o-keyframes cssload-rotate {
  100% {
    -o-transform: rotate(360deg); } }

@-ms-keyframes cssload-rotate {
  100% {
    -ms-transform: rotate(360deg); } }

@-webkit-keyframes cssload-rotate {
  100% {
    -webkit-transform: rotate(360deg); } }

@-moz-keyframes cssload-rotate {
  100% {
    -moz-transform: rotate(360deg); } }

.line-loader {
  height: 4px;
  width: 100%;
  position: relative;
  overflow: hidden;
  background-color: #ddd; }

.line-loader:before {
  display: block;
  position: absolute;
  content: "";
  left: -200px;
  width: 200px;
  height: 4px;
  background-color: #2980b9;
  animation: line-loader-animation 2s linear infinite; }

@keyframes line-loader-animation {
  from {
    left: -200px;
    width: 30%; }
  50% {
    width: 30%; }
  70% {
    width: 70%; }
  80% {
    left: 50%; }
  95% {
    left: 120%; }
  to {
    left: 100%; } }

.dots:after {
  overflow: hidden;
  position: absolute;
  -webkit-animation: ellipsis steps(4, end) 4s infinite;
  animation: ellipsis steps(4, end) 4s infinite;
  content: "...";
  width: 0px; }

loader-backdrop {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.78);
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white; }
  loader-backdrop img {
    width: 100px; }
  loader-backdrop .text {
    font-size: 20px;
    text-align: center; }

.offline-backdrop {
  height: 100%;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 104;
  background: rgba(0, 0, 0, 0.84);
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

.loader-wrapper .logo.dark {
  display: none; }

.dark-mode .loader {
  background-color: rgba(30, 30, 30, 0.95); }

.dark-mode .loader-wrapper {
  background-color: #121212; }
  .dark-mode .loader-wrapper .logo.light {
    display: none; }
  .dark-mode .loader-wrapper .logo.dark {
    display: block; }
