.bdg {
  position: absolute;
  top: -8px;
  right: -8px;
  line-height: 12px;
  background: white;
  padding: 4px;
  border-radius: 50%;
  min-width: 14px;
  min-height: 14px;
  text-align: center;
  font-size: 14px; }

.bdg.light-blue {
  background: #c9f5ff; }

.bdg.blue {
  background: #84d0fd; }

.bdg.green {
  background: #87ec6d; }

.bdg.yellow {
  background: #fff242; }

.bdg.orange {
  background: #ff6d00;
  color: white; }

.bdg.red {
  background: red;
  color: white; }
