.fc button,
.bttn {
  height: 38px;
  padding: 0 20px;
  color: #555;
  font-family: 'Lato', sans-serif;
  background-image: none;
  font-size: 0.71429rem;
  font-weight: 600;
  line-height: 38px;
  letter-spacing: 1px;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border-radius: 6px;
  margin: 0 4px;
  border: 1px solid #989898;
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
  user-select: none;
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
  justify-content: center;
  -webkit-box-align: center;
  -webkit-align-items: center;
  -ms-grid-row-align: center;
  align-items: center;
  -webkit-align-content: center;
  align-content: center;
  max-width: 100%; }

.bttn i {
  line-height: 0; }

.bttn.icon {
  border-radius: 50%;
  padding: 5px;
  height: 35px;
  width: 35px; }

.bttn[disabled],
.fc button.fc-state-disabled {
  opacity: 0.5;
  cursor: not-allowed; }

.bttn:active,
.bttn.pressed,
.fc-state-down,
.fc-state-active {
  -webkit-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  -moz-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  text-shadow: none; }

.bttn.white {
  background-color: white; }

.bttn.blue {
  color: #185698;
  background-color: #84d0fd;
  border-color: #3694f7; }

.bttn.blue i {
  color: #185698; }

.bttn.light-blue {
  color: #188b98;
  background-color: #c9f5ff;
  border-color: #4ecaca; }

.bttn.light-blue i {
  color: #188b98; }

.bttn.red {
  color: #a92222;
  background-color: #ffa5a5;
  border-color: #fd4e4e; }

.bttn.red i {
  color: #a92222; }

.bttn.yellow {
  color: #8a8100;
  background-color: #fff242;
  border-color: #d0c200; }

.bttn.yellow i {
  color: #8a8100; }

.bttn.orange {
  color: #c35300;
  background-color: #fbbc48;
  border-color: #ff6d00; }

.bttn.orange i {
  color: #c35300; }

.bttn.green {
  color: #21771b;
  background-color: #87ec6d;
  border-color: #57af51; }

.bttn.green i {
  color: #21771b; }

.bttn-group,
.fc-button-group {
  box-sizing: border-box;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-direction: row;
  flex-direction: row; }

.bttn-group .bttn:first-child,
.fc-button-group button:first-child {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  margin-right: 0;
  border-right-width: 0; }

.bttn-group .bttn:last-child,
.fc-button-group button:last-child {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: 0; }

.bttn-group .bttn:not(:first-child):not(:last-child),
.fc-button-group button:not(:first-child):not(:last-child) {
  border-radius: 0;
  margin: 0;
  border-right-width: 0; }

.bttn-group .bttn.active,
.fc-button-group button.active {
  -webkit-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  -moz-box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3);
  box-shadow: inset 0 0 7px 0 rgba(0, 0, 0, 0.3); }

.dark-mode .fc button,
.dark-mode .bttn:not(.light-blue):not(.blue):not(.red):not(.orange):not(.yellow):not(.green) {
  color: #c5c5c5; }
