md-dialog-container .zoomContainer {
  z-index: 1000; }

.zoomContainer {
  z-index: 1; }

img-zoom {
  position: relative;
  overflow: hidden; }
  img-zoom .controls {
    position: absolute;
    top: 4px;
    left: 50%;
    transform: translateX(-50%);
    opacity: 0;
    transition: opacity 0.2s ease-in;
    z-index: 3;
    box-sizing: border-box;
    display: -webkit-box;
    display: -webkit-flex;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
    -webkit-flex-direction: row;
    flex-direction: row; }
    img-zoom .controls > * {
      margin: 0 8px;
      background: white; }
  img-zoom .target {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); }

img-zoom:hover .controls {
  transition: opacity 0.2s ease-in;
  opacity: 1; }

@media print {
  img-zoom .controls {
    display: none; } }

.exif {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 1;
  padding: 8px;
  background: rgba(0, 0, 0, 0.23);
  color: white;
  width: 100%;
  display: flex; }
  .exif > * {
    padding-right: 8px; }

md-dialog.image-crop {
  max-width: 95%;
  max-height: 95%; }

.image-crop form {
  max-width: 100%; }

.image-crop .dropzone {
  max-width: 1000px;
  max-height: 100%;
  margin: 24px auto; }

.image-crop .image-wrapper {
  position: relative;
  max-width: 100%;
  max-height: 1080px;
  margin: auto; }

.image-crop .error {
  color: white;
  font-size: 13px;
  text-align: center;
  margin: 8px;
  background: #fd4e4e;
  padding: 8px;
  border-radius: 4px; }

.image-crop form .box .loader-wrapper {
  height: 100%;
  width: 100%;
  top: 0;
  left: 0; }

img-crop {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  overflow: hidden; }
  img-crop canvas {
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    outline: none;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0); }
